:root {
  --dark-background: #060610;
  --menu-background: rgba(25, 25, 25, 0.5);
  --menu-mobile-background: rgba(25, 25, 25);
  --white-background: rgba(255, 255, 255, 0.04);
  --svg-background: rgba(244, 36, 123, 0.2);
  --pink-blur: #ed1871;
  --light-pink: #e93580;
  --btn-pink-gradient: linear-gradient(
    179.34deg,
    #d2008c -47.95%,
    #e6007e 109.59%
  );
  --tg-color: #61cfff;
  --gray-line: rgba(255, 255, 255, 0.5);
  --table-background: #12121c;
  --table-first-background: #0b0b15;
  --article-background: #6e41f3;
  --blockquote-background: rgba(70, 66, 243, 0.2);
  --blockquote-border: #4642f3;
  --tg-border: rgba(118, 246, 255, 0.08);
  --btn-border: rgba(255, 255, 255, 0.1);
  --btn-box-shadow-s: rgba(237, 24, 113, 0.4);
  --btn-box-shadow-e: rgba(230, 0, 126, 0.2);
  --border-gray: rgba(255, 255, 255, 0.2);
  --gray-color: #949494;
  --light-transparent-color: rgba(255, 255, 255, 0.3);
  --mail-background: rgba(255, 255, 255, 0.08);
  --blue-border: rgba(58, 144, 255, 0.08);
  --light-color: rgba(255, 255, 255, 0.8);
  --arrow-background: rgba(255, 255, 255, 0.02);
  --arrow-border: rgba(255, 255, 255, 0.06);
  --arrow-box-shadow1: rgba(255, 255, 255, 0.25);
  --arrow-box-shadow2: rgba(0, 0, 0, 0.16);
  --font-inter: "Inter";
  --font-unbounded: "Unbounded";
}

body {
  background: var(--dark-background);
  font-size: 1.3125rem;
  color: var(--bs-white);
  min-height: 100vh;
  overflow-x: hidden;
  max-width: 100vw;
}
body * {
  font-family: var(--font-inter);
}

button {
  outline: none;
}

.container {
  max-width: 1782px;
  padding: 0 1.3125rem;
  position: relative;
}
@media (max-width: 768px) {
  .container {
    padding: 0 1rem;
  }
}
@media (max-width: 1780px) {
  .container {
    overflow: hidden;
  }
}

p {
  margin-bottom: 0;
  hyphens: auto;
}

a {
  color: inherit;
  text-decoration: none;
}
a:hover {
  color: inherit;
}

ul {
  list-style-type: none;
  margin-bottom: 0;
}

h2.fs-2 {
  font-weight: 600;
  position: relative;
  z-index: 0;
}
@media (max-width: 992px) {
  h2.fs-2 {
    font-size: 2rem !important;
  }
}
@media (max-width: 550px) {
  h2.fs-2 {
    font-size: 1.3125rem !important;
  }
}

.hidden {
  overflow: hidden;
}

.main {
  padding-top: 7rem;
}
.main h1 {
  font-weight: 500;
  font-family: var(--font-unbounded);
}

.default-background {
  background: var(--white-background);
  border: 2px solid var(--white-background);
  -webkit-backdrop-filter: blur(36px);
  backdrop-filter: blur(36px);
}

.default-list {
  list-style-type: disc;
}

@keyframes circles_move {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(5deg);
  }
  75% {
    transform: rotate(-10deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
.circle-1-wrapper {
  position: absolute;
  top: -49vw;
  right: 27.25vw;
  width: 100vw;
  height: 100vw;
}
@media (max-width: 700px) {
  .circle-1-wrapper {
    right: inherit;
  }
}
@media (max-width: 450px) {
  .circle-1-wrapper {
    top: -34vw;
  }
}

.circle-1 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: radial-gradient(90% 90% at -6% 29%, rgba(210, 0, 140, 0) 0%, #d20043 100%);
  mix-blend-mode: color-dodge;
  z-index: -1;
  pointer-events: none;
  overflow: hidden;
  animation: circle1_move 35.5s cubic-bezier(0.4, 0, 0.2, 1) infinite;
}
@media (max-width: 768px) {
  .circle-1 {
    animation: circle1_move1 35.5s cubic-bezier(0.4, 0, 0.2, 1) infinite;
  }
}
@media (max-width: 700px) {
  .circle-1 {
    animation: circle1_move2 35.5s cubic-bezier(0.4, 0, 0.2, 1) infinite;
    height: 93vh;
    width: 90vh;
  }
}
.circle-1::after {
  content: "";
  width: 10px;
  height: 10px;
  border-radius: 50%;
  position: absolute;
  top: 80%;
  right: 5%;
  background: red;
}
.circle-1 .purple-circle {
  position: absolute;
  width: 60%;
  height: 60%;
  border-radius: 50%;
  opacity: 0.4;
  z-index: 3;
  top: -13%;
  left: -24%;
  background: radial-gradient(50% 50% at 50% 50%, rgba(255, 255, 255, 0.54) 0%, rgba(255, 255, 255, 0) 100%);
}

@keyframes circle1_move {
  0% {
    transform: rotate(180deg);
  }
  25% {
    transform: rotate(170deg);
  }
  50% {
    transform: rotate(180deg);
  }
  75% {
    transform: rotate(190deg);
  }
  100% {
    transform: rotate(180deg);
  }
}
@keyframes circle1_move1 {
  0% {
    transform: rotate(190deg);
  }
  25% {
    transform: rotate(180deg);
  }
  50% {
    transform: rotate(190deg);
  }
  75% {
    transform: rotate(195deg);
  }
  100% {
    transform: rotate(190deg);
  }
}
@keyframes circle1_move2 {
  0% {
    transform: rotate(170deg);
  }
  25% {
    transform: rotate(180deg);
  }
  50% {
    transform: rotate(170deg);
  }
  75% {
    transform: rotate(165deg);
  }
  100% {
    transform: rotate(170deg);
  }
}
.white-circle-1 {
  position: absolute;
  width: 55%;
  height: 55%;
