  offset-path: path("M 1500 400 C 900 470 400 450 -300 220");
  offset-rotate: auto;
  animation: circle1 13s cubic-bezier(0.4, 0, 0.2, 1) paused;
}

@keyframes circle1 {
  0% {
    offset-distance: 0%;
  }
  60% {
    offset-distance: 50%;
  }
  100% {
    offset-distance: 100%;
  }
}
.white-circle-2 {
  width: 65%;
  height: 65%;
  position: absolute;
  top: 0;
  background: radial-gradient(50% 50% at 50% 50%, rgba(255, 255, 255, 0.54) 0%, rgba(255, 255, 255, 0.46) 2.67%, rgba(255, 255, 255, 0.39) 7.19%, rgba(255, 255, 255, 0.3) 14.13%, rgba(255, 255, 255, 0.24) 18.21%, rgba(255, 255, 255, 0.19) 24.7%, rgba(255, 255, 255, 0.15) 31.68%, rgba(255, 255, 255, 0.12) 37.41%, rgba(255, 255, 255, 0.1) 41.99%, rgba(255, 255, 255, 0.09) 48.86%, rgba(255, 255, 255, 0.06) 59.16%, rgba(255, 255, 255, 0.04) 66.03%, rgba(255, 255, 255, 0.03) 76.46%, rgba(255, 255, 255, 0.01) 90.61%, rgba(255, 255, 255, 0) 100%);
  offset-path: path("M 1500 400 C 900 470 400 450 -300 220");
  offset-rotate: auto;
  animation: circle2 13s cubic-bezier(0.4, 0, 0.2, 1) paused;
}

@keyframes circle2 {
  0% {
    offset-distance: 0%;
  }
  40% {
    offset-distance: 40%;
