  animation: small_circle1 10s cubic-bezier(0.4, 0, 0.2, 1) paused;
}

@keyframes small_circle1 {
  0% {
    offset-distance: 0%;
  }
  45% {
    offset-distance: 55%;
  }
  100% {
    offset-distance: 100%;
  }
}
.middle-circle-1 {
  width: 35%;
