  offset-rotate: auto;
  animation: middle_circle3 10s cubic-bezier(0.4, 0, 0.2, 1) paused;
}

@keyframes middle_circle3 {
  0% {
    offset-distance: 0%;
  }
  45% {
    offset-distance: 55%;
  }
  100% {
    offset-distance: 100%;
  }
}
.circle-2 {
  position: absolute;
  top: 10vw;
  left: 65.6vw;
  width: 69.2vw;
  height: 69.2vw;
  border-radius: 50%;
  background: radial-gradient(90% 90% at -6% 29%, rgba(210, 0, 140, 0) 0%, #d20043 100%);
  z-index: -1;
  pointer-events: none;
  overflow: hidden;
  mix-blend-mode: color-dodge;
