}
.circle-2 .white-circle-1 {
  animation: circle2_1 13s cubic-bezier(0.4, 0, 0.2, 1) paused;
  animation-delay: 5.6s;
  width: 45%;
  height: 45%;
}
.circle-2 .white-circle-2 {
  animation: circle2_2 13s cubic-bezier(0.4, 0, 0.2, 1) paused;
  animation-delay: 6.7s;
  width: 50%;
