.circle-2 .white-circle-small {
  animation: small_circle2 10s linear paused;
  position: absolute;
  top: 0;
  width: 20%;
  height: 20%;
}
.circle-2 .middle-circle-1 {
  animation: middle_circle2 10s cubic-bezier(0.4, 0, 0.2, 1) paused;
  animation-delay: 4.3s;
  width: 30%;
  height: 30%;
}
.circle-2 .middle-circle-2 {
  width: 35%;
  height: 35%;
  animation: middle_circle4 7s linear paused;
  animation-delay: 4.8s;
}

@keyframes circle2_move {
  0% {
    transform: rotate(75deg) translate(0, 0);
  }
  50% {
    transform: rotate(80deg) translate(0, 0);
  }
  75% {
    transform: rotate(55deg) translate(50px, -50px);
  }
  100% {
    transform: rotate(70deg) translate(0, 0);
  }
}
@keyframes circle2_move2 {
  0% {
    transform: rotate(75deg) translate(0, 0);
  }
  50% {
    transform: rotate(80deg) translate(0, 0);
  }
  75% {
    transform: rotate(70deg) translate(15px, -15px);
  }
  100% {
    transform: rotate(75deg) translate(0, 0);
  }
}
@keyframes circle2_1 {
  0% {
    offset-distance: 0%;
